import React, {useEffect, useState} from "react";
import { PoweroffOutlined, SettingOutlined } from '@ant-design/icons';

import styles from './index.module.scss'
import {Link} from "react-router-dom";

const HeaderInfo = () => {
  const [time, setTime] = useState(new Date());
  useEffect(() => {
    setInterval(() => {
      setTime(new Date());
    }, 1000)
  }, []);
    return (
      <div className={styles['bl-header']}>
        <div className={styles.title}>智慧防疫系统</div>
        <div className={styles.rightInfo}>
          <span className={styles.item}>当前时间：{`${time.getFullYear()}年${time.getMonth()+1}月${time.getDay()}日${time.getHours()}:${time.getMinutes()}:${time.getSeconds() < 10 ? `0${time.getSeconds()}`: time.getSeconds()}`}</span>
          <span className={styles.item}><Link to='/setting'><SettingOutlined />设置</Link></span>
          <span className={styles.item}><Link to='/login'><PoweroffOutlined />退出</Link></span>
        </div>
      </div>
    );
};

export default HeaderInfo;
