import React, {useState} from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route, Link,
} from "react-router-dom";
import Home from "./containers/Home";
import About from "./containers/Company";
import {Layout, Menu, Breadcrumb, Button} from 'antd';
import { UploadOutlined, VideoCameraOutlined, AppstoreOutlined, DesktopOutlined, MailOutlined,ContainerOutlined, PieChartOutlined,UserOutlined, LaptopOutlined, NotificationOutlined, ForkOutlined, GlobalOutlined, MenuUnfoldOutlined, MenuFoldOutlined } from '@ant-design/icons';
import styles from './App.module.scss'
import HeaderInfo from "./containers/HeaderInfo";
import Settings from "./containers/Setting";
import WifiCom from "./containers/Wifi";
import Path from "./containers/Path";
import Isolation from "./containers/Isolation";
import Monitor from "./containers/Monitor";
import RoutesCom from "./containers/Routes";
import City from "./containers/City";
import AntiVirus from "./containers/AntiVirus";
import LoginCom from "./containers/Login";

const { SubMenu } = Menu;
const { Header, Content, Footer, Sider } = Layout;

const tabs = [{
  value: 'info-collection',
  label: '人员信息采集'
},{
  value: 'anti-virus',
  label: '智慧消杀监测'
},{
  value: 'isolation',
  label: '居家隔离管理'
},{
  value: 'data-analysis',
  label: '数据统计分析'
},{
  value: 'city-data',
  label: '城际数据管理'
},{
  value: 'path-analysis',
  label: '路线行径分析'
},{
  value: 'monitor-analysis',
  label: '监测点位分析'
}];

const App = () => {
  const [collapsed, setToggleCollapsed] = useState(false);
  return (
          <div className={styles.app}>
            <Router>
              <HeaderInfo />
              <Layout style={{ height: '100%' }}>
                <Sider
                  style={{height: 'calc(100vh - 40px)'}}
                  breakpoint="lg"
                  collapsedWidth="0"
                  onBreakpoint={broken => {
                    console.log(broken);
                  }}
                  onCollapse={(collapsed, type) => {
                    console.log(collapsed, type);
                  }}
                >
                  <Menu
                    defaultSelectedKeys={['1']}
                    defaultOpenKeys={['sub1', 'sub2', 'sub3']}
                    mode="inline"
                    theme="dark"
                    inlineCollapsed={collapsed}
                    style={{height: '100%', overflow: 'auto'}}
                  >
                    <Menu.Item key="1" icon={<PieChartOutlined />}>
                      <Link to="/">数据监控</Link>
                    </Menu.Item>
                    <SubMenu key="sub1" icon={<MailOutlined />} title="人员信息采集">
                      <Menu.Item key="5">
                        <Link to="/wifi">wifi探针</Link>
                      </Menu.Item>
                      <SubMenu key="sub2" icon={<MailOutlined />} title="LAC基站">
                        <Menu.Item key="7">
                          <Link to="/path">轨迹分析</Link>
                        </Menu.Item>
                        <Menu.Item key="8">
                          <Link to="/company">密切接触者分析</Link>
                        </Menu.Item>
                      </SubMenu>
                    </SubMenu>
                    <SubMenu key="sub3" icon={<MailOutlined />} title="流调隔离">
                      <Menu.Item key="31">
                        <Link to="/monitor">交通卡口人员归属地识别</Link>
                      </Menu.Item>
                      <Menu.Item key="32">
                        <Link to="/monitor">手机进出异常监测</Link>
                      </Menu.Item>
                      <Menu.Item key="33">
                        <Link to="/monitor">隔离托管追查</Link>
                      </Menu.Item>
                      <Menu.Item key="34">
                        <Link to="/monitor">集中隔离出入监控</Link>
                      </Menu.Item>
                      <Menu.Item key="36">
                        <Link to="/monitor">感染者信息管理</Link>
                      </Menu.Item>
                    </SubMenu>
                    <Menu.Item key="12" icon={<DesktopOutlined />}>
                      <Link to="/anti-virus">智慧消杀监测</Link>
                    </Menu.Item>
                    <Menu.Item key="13" icon={<ContainerOutlined />}>
                      <Link to="/isolation">居家隔离管理</Link>
                    </Menu.Item>
                    <Menu.Item key="14" icon={<ContainerOutlined />}>
                      <Link to="/city">城际数据管理</Link>
                    </Menu.Item>
                    <Menu.Item key="15" icon={<ContainerOutlined />}>
                      <Link to="/routes">路线行径分析</Link>
                    </Menu.Item>
                    <Menu.Item key="16" icon={<ContainerOutlined />}>
                      <Link to="/monitor">监测点位</Link>
                    </Menu.Item>
                  </Menu>
                </Sider>
                <Layout>
                  <Content style={{ margin: '24px 16px 0' }}>
                    <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
                      <Switch>
                        <Route path="/" exact>
                          <Home />
                        </Route>
                        <Route path="/wifi">
                          <WifiCom />
                        </Route>
                        <Route path="/anti-virus">
                          <AntiVirus />
                        </Route>
                        <Route path="/company">
                          <About />
                        </Route>
                        <Route path="/isolation">
                          <Isolation />
                        </Route>
                        <Route path="/path">
                          <Path/>
                        </Route>
                        <Route path="/city">
                          <City />
                        </Route>
                        <Route path="/monitor">
                          <Monitor />
                        </Route>
                        <Route path="/routes">
                          <RoutesCom />
                        </Route>
                        <Route path="/setting">
                          <Settings />
                        </Route>
                        <Route path="/login">
                          <LoginCom />
                        </Route>
                      </Switch>
                    </div>
                  </Content>
                  <Footer style={{ textAlign: 'center' }}>Ant Design ©2021 Created by LanGe</Footer>
                </Layout>
              </Layout>
            </Router>
          </div>
  );
}

export default App;
